import { gsap }          from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);
const animationScroll = () => {
	gsap.utils.toArray('.graphics__item').forEach((el, index) => {
		let start;
		let end;
		if (window.matchMedia('(max-width: 700px)').matches) {
			start = `top+=${100} center-=${140 * index - 2}`;
			end   = `+=${700}`;
		} else if (window.matchMedia('(min-width: 701px) and (max-width: 1200px)').matches) {
			start = `top+=${60} center-=${110 * index - 2}`;
			end   = `+=${700}`;
		} else {
			start = `top+=${40} center-=${90 * index - 2}`;
			end   = `+=${700}`;
		}

		const tls = gsap.timeline({
			scrollTrigger : {
				trigger       : el,
				start,
				end,
				// markers       : true,
				toggleClass   : 'active',
				toggleActions : 'pause',
			},
		});
	});

	const parent = document.querySelector('.expertise-item__img');

	gsap.to('.iphone-scroll__item--one', {
		scrollTrigger : {
			trigger : parent,
			start   : 'top+=100px center',
			end     : 'bottom-=100px center',
			scrub   : true,
			// markers : true,
		},
		y       : '-312px',
		ease    : 'none',
		stagger : 200,
	});

	const mm = gsap.matchMedia();

	mm.add('(min-width: 1080px)', () => {
		gsap.to('.iphone-scroll__item--two', {
			scrollTrigger : {
				trigger : parent,
				start   : 'top+=100px center',
				end     : 'bottom-=100px center',
				scrub   : true,
				// markers : true,
			},
			y       : '-312px',
			ease    : 'none',
			stagger : 200,
		});
	});

	mm.add('(min-width: 744px)', () => {
		gsap.to('.iphone-scroll__item--two', {
			scrollTrigger : {
				trigger : parent,
				start   : 'top+=1110px center',
				end     : 'bottom+=910px center',
				scrub   : true,
				// markers : true,
			},
			y       : '-312px',
			ease    : 'none',
			stagger : 0,
		});
	});

	mm.add('(max-width: 640px)', () => {
		gsap.to('.iphone-scroll__item--two', {
			scrollTrigger : {
				trigger : parent,
				start   : 'top+=1610px center',
				end     : 'bottom+=1410px center',
				scrub   : true,
				// markers : true,
			},
			y       : '-312px',
			ease    : 'none',
			stagger : 0,
		});
	});
};

export default animationScroll;
