import {gsap} from 'gsap';

const cursorAnim = () => {
	const cursorBee = document.querySelector('.js-cursor-bee');
	const cursorEye = document.querySelector('.js-cursor-plus');

	let scale = 1;

	function mousemoveHandler(e) {
		if (cursorEye) {
			const target = e.target;
			const tl = gsap.timeline({
				defaults: {
					x: e.clientX,
					y: e.clientY,
					ease: 'power2.out',
				},
			});

			if (target.closest('[data-cursor=""]')) {
				tl.to(cursorBee, {
					opacity: 0,
				}).to(
					cursorEye,
					{
						opacity: 0.9,
					},
					'-=0.45'
				);
			} else {
				if (target.classList.contains('line-animation')) {
					scale = 1;
				} else {
					scale = 1;
				}

				tl.to(cursorBee, {
					opacity: 0.9,
					scale: scale,
				}).to(
					cursorEye,
					{
						opacity: 0,
					},
					'-=0.45'
				);
			}
		}
	}

	function mouseleaveHandler() {
		if (cursorBee) {
			gsap.to(cursorBee, {
				opacity: 0,
			});
		}
	}

	document.addEventListener('mousemove', mousemoveHandler);
	document.addEventListener('mouseleave', mouseleaveHandler);
};

export default cursorAnim;
