import Chart                 from 'chart.js/auto';
import { getRelativePosition } from 'chart.js/helpers';

const aboutAnim = () => {
	const nums    = document.querySelectorAll('.number');
	const section = document?.querySelector('.about');
	let start     = false;

	function createChart(elementId, data) {
		const ctx   = document.getElementById(elementId);
		const chart = new Chart(ctx, {
			type : 'doughnut',
			data : {
				datasets : [
					{
						data,
						backgroundColor : [
							'rgba(213,107,196,0.98)',
							'rgb(13, 13, 88)',
							'rgb(2, 2, 36)',
							'rgb(89, 63, 240)',
						],
						hoverOffset : 0,
						radius      : 55.5,
						cutout      : 48,
						rotation    : -40,
						borderColor : '#FFf',
						borderWidth : 1,
						spacing     : 0,
					},
				],
			},
			options : {
				plugins : {
					tooltip : {
						enabled : false,
					},
				},
				hover : {
					intersect : false,
				},
			},
		});

		// chart.canvas.style.height = '100%';
		// chart.canvas.style.width = '100%';
		return chart;
	}

	if (section) {
		window.onscroll = function () {
			if (window.scrollY >= section.offsetTop) {
				if (!start) {
					nums.forEach((num) => {
						startCount(num);
					});
					const chart1 = createChart('myChart1', [10, 11, 5, 17]);
					const chart2 = createChart('myChart2', [10, 11, 5, 17]);
					const chart3 = createChart('myChart3', [10, 11, 5, 17]);
					const chart4 = createChart('myChart4', [10, 11, 5, 17]);
					const chart5 = createChart('myChart5', [10, 11, 5, 17]);

					setTimeout(() => {
						chart1.data.datasets[0].data = [18, 12, 7, 10]; //     
						chart1.update();
					}, 1200);

					setTimeout(() => {
						chart2.data.datasets[0].data = [12, 19, 11, 10]; //     
						chart2.update();
					}, 2400);

					setTimeout(() => {
						chart3.data.datasets[0].data = [10, 7, 17, 7]; //     
						chart3.update();
					}, 1200);

					setTimeout(() => {
						chart4.data.datasets[0].data = [10, 7, 10, 17]; //     
						chart4.update();
					}, 2400);

					setTimeout(() => {
						chart5.data.datasets[0].data     = [7, 7, 10, 10]; //     
						chart5.data.datasets[0].rotation = -140;
						chart5.update();
					}, 2400);

					start = true;
				}
			}
		};

		function startCount(el) {
			const {
					  goal,
					  speed,
					  step,
				  }             = el.dataset;
			const decimalPlaces = (goal.split('.')[1] || []).length;
			const increment     = step ? parseFloat(step) : 1 / 10 ** decimalPlaces;
			let currentVal      = parseFloat(el.textContent);
			const count         = setInterval(() => {
				currentVal += increment;
				if (currentVal >= parseFloat(goal)) {
					el.textContent = parseFloat(goal).toFixed(decimalPlaces);
					clearInterval(count);
				} else {
					el.textContent = currentVal.toFixed(decimalPlaces);
				}
			}, speed || 40);
		}
	}
};

export default aboutAnim;
