const filterWorks = () => {
	const btn = document.querySelectorAll('[data-filter]');
	const btnAll = document.querySelector('[data-filter-all]');
	const workItem = document.querySelectorAll('[data-filter-work]');

	btn.forEach((el) => {
		el.addEventListener('change', () => {
			const activeCategory = el.getAttribute('data-filter');
			let btnChecked = document.querySelectorAll("[data-filter]:checked").length;

			if (btnChecked !== 0) {
				if (el.checked) {
					btnAll.classList.remove('is-active');
					workItem.forEach((item) => {
						item.classList.remove('is-show');
					});
	
					const workActive = document.querySelectorAll(`.${activeCategory}`);
					workActive.forEach((item) => {
						item.classList.add('is-active');
					});
				} else {
					const workNoActive = document.querySelectorAll(`.${activeCategory}`);
					workNoActive.forEach((itemNoActive) => {
						itemNoActive.classList.remove('is-active');
					});

					let btnCheckedSublings = document.querySelectorAll("[data-filter]:checked");

					btnCheckedSublings.forEach((itemNoActive) => {
						let itemCheckedSublings = itemNoActive.getAttribute('data-filter');
						const unutCheckedSublings = document.querySelectorAll(`.${itemCheckedSublings}`);

						unutCheckedSublings.forEach((itemSublings) => {
							itemSublings.classList.add('is-active');
						});
					});
				}
			} else {
				workItem.forEach((item) => {
					item.classList.remove('is-active');
					item.classList.add('is-show');
					btnAll.classList.add('is-active');
				});
		
				btn.forEach((el) => {
					el.checked = false;
				});
			}
		});
	});

	if (btnAll) {
		btnAll.addEventListener('click', () => {
			workItem.forEach((item) => {
				item.classList.remove('is-active');
				item.classList.add('is-show');
				btnAll.classList.add('is-active');
			});
	
			btn.forEach((el) => {
				el.checked = false;
			});
		});
	}
};

export default filterWorks;
