const navServices = () => {
	const links           = document.querySelectorAll('.services-nav__link');
	const servicesWrapper = document.querySelector('.services-wrapper');
	const servicesNav     = document.querySelector('.services-nav');

	window.addEventListener('scroll', () => {
		if (servicesWrapper) {
			const servicesWrapperHeight = servicesWrapper.offsetHeight;
			const scrollPosition        = window.pageYOffset || document.documentElement.scrollTop;

			const servicesWrapperTop = servicesWrapper.getBoundingClientRect().top + scrollPosition;

			if (scrollPosition > servicesWrapperTop && scrollPosition < (servicesWrapperTop + servicesWrapperHeight)) {
				servicesNav.classList.add('active');
			} else {
				servicesNav.classList.remove('active');
			}
		}

		links.forEach((link) => {
			const sectionId   = link.getAttribute('href').substring(1);
			const section     = document.getElementById(sectionId);
			const rectSection = section.getBoundingClientRect();
			if (rectSection.top <= 140 && rectSection.bottom >= 140) {
				link.classList.add('active');
			} else {
				link.classList.remove('active');
			}
		});
	});
};

export default navServices;
