// import Typed from 'typed.js';
import { gsap }         from 'gsap'; 
import TextPlugin from 'gsap/TextPlugin';

gsap.registerPlugin(TextPlugin); 

const typed = () => { 
	const heroItem = Array.from(document.querySelectorAll('[data-hero-item=""]'));
	const heroitemUnit = document.querySelector('[data-hero-item=""]');
	const heroSvg  = Array.from(document.querySelectorAll('.hero__svg'));
	let currentIndex = 0;

	function animateHeroItems() {
		if (heroitemUnit) {
			//   'active'  'svg-animate'   
			heroItem.forEach((item, index) => {
				item.classList.remove('active');
				if (heroSvg[index]) {
					heroSvg[index].classList.remove('svg-animate');
				}
			});

			//   'active'  'svg-animate'   
			heroItem[currentIndex].classList.add('active');

			if (heroSvg[currentIndex]) {
				heroSvg[currentIndex].classList.add('svg-animate');
			}

			// createTyped(typedElements[currentIndex].id, typedElements[currentIndex].text, 50);
			const typedText = document.querySelectorAll('[data-title-typed]');
			typedText.forEach(item => {
				let tx = item.getAttribute('data-title-typed');
				let tl = gsap.to(item, {
				  text: tx,
				  duration: 4,
				  ease: 'power0.linear',
				  yoyo: false,
				});
				setTimeout(function() {
					tl.restart();
				}, 5000);
			});

			currentIndex = (currentIndex + 1) % heroItem.length; //  

			setTimeout(animateHeroItems, 5000);
		}
	}
	animateHeroItems();
};

export default typed;