const burger = () => {
	const burgerEl      = document?.querySelector('[data-burger]');
	const menu          = document?.querySelector('[data-menu]');
	const targetElement = document.querySelector('body');
	const close         = document?.querySelector('[data-close]');

	function disableScroll() {
		const pagePosition = window.scrollY;
		targetElement.classList.add('scroll');
		targetElement.dataset.position = pagePosition;
	}

	function enableScroll() {
		targetElement.classList.remove('scroll');
		targetElement.removeAttribute('data-position');
	}

	burgerEl?.addEventListener('click', () => {
		menu?.classList.add('active');
		burgerEl?.setAttribute('aria-expanded', 'true');
		burgerEl?.setAttribute('aria-label', 'lose menu');
		document.querySelector('.header__logo').classList.add('header__logo--mobile')
		disableScroll();
	});

	close?.addEventListener('click', () => {
		menu?.classList.remove('active');
		burgerEl?.setAttribute('aria-expanded', 'false');
		burgerEl?.setAttribute('aria-label', 'Open menu');
		document.querySelector('.header__logo').classList.remove('header__logo--mobile')
		enableScroll();
	});

};

export default burger;
